// /* ===== Qobilatlarim ===== */
.qobilat {
    margin: 0;
    padding: 0;
    height: 100vh;
    // display: flex;
    // align-items: center;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;

    h1 {
        font-size: 40px;
        font-weight: 900;
        color: whitesmoke;
        font-family: Georgia, Times, 'Times New Roman', serif;
        margin-top: 40px;
        margin-bottom: 40px;
        text-align: center;
    }


    progres-bar {
        display: flex;
        flex-direction: column;
        // margin-top: 20px;
        // margin-bottom: 20px;


        .label {
            font-size: 40px;
            font-family: Arial, sans-serif;
        }

        .line {
            width: 600px;
            height: 15px;
            background: #E8E8E8;
            border-radius: 10px;
            position: relative;

            span {
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                background: red;
                border-radius: 10px;
            }
        }
    }
}